import React from "react";
import wrapper from "../commons/WebComponentWrapper";
import { Button } from 'carbon-components-react';
import "carbon-components/scss/globals/scss/styles.scss";

const dispatchedEvents = ["HOME#clicked"];

const Home = function(props, ref) {
    return view(props, ref);
}

const view = (props, ref) => {
    console.log("In Home ", props);
    return (<div ref={ref}> 
        <div>Home Page</div>
        <Button kind='danger' size='sm' onClick={() => {
            props.dispatchEvent("HOME#clicked", { message: 'Home button was clicked' });
        }}>{props.message}</Button>
    </div>);
}

const styles = `
    
`

const props = [{
    name: "message",
    defaultValue: "Click me",
    type: "string"
}]

const WrappedComponent = wrapper(React.forwardRef(Home), props);
export {WrappedComponent, styles, props, dispatchedEvents};